import "@servicenow/now-button";
import "@servicenow/now-avatar";
import "@servicenow/now-modal";
import { t } from "sn-translate";
import { isEmpty } from "lodash";
import { controlElementFactory as glideControlElementFactory } from "@devsnc/sn-glide-form-controls";
import { createOnValueChangeHandler } from "./actionHandlers";
import {
	SC_FORM_VALUE_CHANGE,
	VALIDATE_REQUESTED_FOR,
	VARIABLE_TYPE,
	REQUESTED_FOR_TEXT
} from "./constants";

export const view = (state, { dispatch }) => {
	const {
		properties: { requestedForDetails, catItemSysId },
		modalOpened,
		submitDisabled,
		currentRequestFor
	} = state;

	if (isEmpty(requestedForDetails) || isEmpty(catItemSysId)) return;

	const { displayValue, readonly } = requestedForDetails;

	const { props } = glideControlElementFactory.getFormControl(
		requestedForDetails.name,
		VARIABLE_TYPE,
		{
			...requestedForDetails,
			...currentRequestFor
		},
		createOnValueChangeHandler(SC_FORM_VALUE_CHANGE, dispatch),
		null,
		{
			tableName: "sc_cat_item",
			sysId: catItemSysId
		}
	);

	return (
		<div>
			<div className="sn-catalog-request-for">
				<now-avatar size="sm" user-name={displayValue} />
				<span className="sn-catalog-request-for-text">
					{REQUESTED_FOR_TEXT}
				</span>
				<now-button-bare label={displayValue} disabled={!!readonly} size="md" />
			</div>

			<now-modal
				opened={modalOpened}
				footer-actions={[
					{
						id: "save",
						variant: "primary",
						label: "Submit",
						disabled: submitDisabled,
						clickActionType: VALIDATE_REQUESTED_FOR
					}
				]}
				header-label={t("Tell us who needs this request")}
				size="md"
			>
				<sn-record-reference-connected {...props} />
			</now-modal>
		</div>
	);
};
