import { createHttpEffect } from "@servicenow/ui-effect-http";
import {
	DELEGATION_API_URL,
	REQUESTED_FOR_ERROR_MSG,
	ERROR,
	NOW_BUTTON_BARE_CLICKED,
	NOW_MODAL_OPENED_SET,
	SC_FORM_VALUE_CHANGE,
	FETCH_DELEGATION_ACCESS,
	DELEGATION_DATA_FETCH_SUCCESS,
	VALIDATE_REQUESTED_FOR,
	DELEGATION_DATA_FETCH_FAILURE,
	HIDE_REQUEST_FOR_FIELD_MESSAGE,
	SHOW_REQUEST_FOR_FIELD_MESSAGE,
	GFORM_SET_VALUE,
	DELEGATION_DATA_FETCH_FAILURE_MSG
} from "./constants";

const openModal = ({ updateState }) => {
	updateState({
		modalOpened: true
	});
};

const handleValueChange = ({ dispatch, action, updateState, properties }) => {
	const { value, displayValue } = action.payload;
	const {
		requestedForDetails: { name }
	} = properties;

	dispatch(HIDE_REQUEST_FOR_FIELD_MESSAGE, { name: name });
	updateState({
		currentRequestFor: {
			value: value,
			displayValue: displayValue
		},
		submitDisabled: false
	});
};

const validateRequestForUser = ({ dispatch, state, properties }) => {
	const {
		currentRequestFor: { value }
	} = state;
	const { catItemSysId } = properties;
	dispatch(FETCH_DELEGATION_ACCESS, {
		user_sys_id: value,
		item_sys_id: catItemSysId
	});
};

const fetchDelegationAccess = createHttpEffect(DELEGATION_API_URL, {
	method: "GET",
	pathParams: ["user_sys_id", "item_sys_id"],
	successActionType: DELEGATION_DATA_FETCH_SUCCESS,
	errorActionType: DELEGATION_DATA_FETCH_FAILURE
});

const handleDelegationDataFetchSuccess = ({
	dispatch,
	action,
	state,
	properties,
	updateState
}) => {
	const {
		result: { result }
	} = action.payload;
	const { currentRequestFor } = state;
	const {
		requestedForDetails: { name }
	} = properties;
	const { value, displayValue } = currentRequestFor;

	if (result) {
		updateState({
			modalOpened: false,
			submitDisabled: true
		});
		dispatch(GFORM_SET_VALUE, { fieldName: name, value, displayValue });
	} else {
		dispatch(HIDE_REQUEST_FOR_FIELD_MESSAGE, { name: name });
		dispatch(SHOW_REQUEST_FOR_FIELD_MESSAGE, {
			name: name,
			message: REQUESTED_FOR_ERROR_MSG,
			type: ERROR
		});
	}
};

const handleDelegationDataFetchFailure = ({ dispatch, state }) => {
	const {
		properties: {
			requestedForDetails: { name }
		}
	} = state;
	dispatch(HIDE_REQUEST_FOR_FIELD_MESSAGE, { name: name });
	dispatch(SHOW_REQUEST_FOR_FIELD_MESSAGE, {
		name: name,
		message: DELEGATION_DATA_FETCH_FAILURE_MSG,
		type: ERROR
	}); //translatable
};

const handleModalClosed = ({ dispatch, properties, updateState }) => {
	const {
		requestedForDetails: { name, value, displayValue }
	} = properties;

	dispatch(HIDE_REQUEST_FOR_FIELD_MESSAGE, { name: name });
	updateState({
		modalOpened: false,
		submitDisabled: true,
		currentRequestFor: {
			value: value,
			displayValue: displayValue
		}
	});
};

export const createOnValueChangeHandler = (actionName, dispatch) => (
	event,
	fieldName,
	value,
	displayValue,
	error
) => dispatch(actionName, { value, displayValue, error });

const hideRequestForFieldMessage = ({ state, updateState }) => {
	const { currentRequestFor } = state;

	updateState({
		currentRequestFor: {
			...currentRequestFor,
			messages: []
		}
	});
};

const showRequestForFieldMessage = ({ state, action, updateState }) => {
	const { message, type } = action.payload;
	const { currentRequestFor } = state;
	const { messages } = currentRequestFor;
	const newMessages = messages || [];

	newMessages.push({ type, message });

	updateState({
		currentRequestFor: {
			...currentRequestFor,
			messages: newMessages
		}
	});
};

export default {
	[NOW_BUTTON_BARE_CLICKED]: openModal,
	[SC_FORM_VALUE_CHANGE]: handleValueChange,
	[VALIDATE_REQUESTED_FOR]: validateRequestForUser,
	[FETCH_DELEGATION_ACCESS]: fetchDelegationAccess,
	[DELEGATION_DATA_FETCH_SUCCESS]: handleDelegationDataFetchSuccess,
	[DELEGATION_DATA_FETCH_FAILURE]: handleDelegationDataFetchFailure,
	[NOW_MODAL_OPENED_SET]: handleModalClosed,
	[HIDE_REQUEST_FOR_FIELD_MESSAGE]: hideRequestForFieldMessage,
	[SHOW_REQUEST_FOR_FIELD_MESSAGE]: showRequestForFieldMessage
};
