export default (state) => {
    const { properties: { requestedForDetails }, currentRequestFor } = state;

    if (!requestedForDetails)
        return;

    return {
        ...state,
        currentRequestFor: {
            ...currentRequestFor,
            value: currentRequestFor.value || requestedForDetails.value,
            displayValue: currentRequestFor.displayValue || requestedForDetails.displayValue
        }
    }
};
