import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import { view } from "./view";
import styles from "./styles.scss";
import actions from "./actions";
import actionHandlers from "./actionHandlers";
import transformState from "./transformState";

/**
 * Displays the requested for details of given Catalog item or Order guide.
 *
 * ```jsx
 * <sn-catalog-request-for
 *		catItemSysId={...}
 *		requestedForDetails={...}
 *		/>
 * ```
 *
 * @seismicElement sn-catalog-request-for
 * @summary Displays the requested for details of given Catalog item or Order guide.
 *
 */
createCustomElement("sn-catalog-request-for", {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		/**
		 * Catalog item sysId,
		 *
		 * @type {string}
		 */
		catItemSysId: {
			default: {}
		},
		/**
		 * Requested for user details
		 *
		 * @type {string}
		 */
		requestedForDetails: {
			default: {}
		}
	},
	initialState: {
		modalOpened: false,
		submitDisabled: true,
		currentRequestFor: {
			value: null,
			displayValue: null,
			messages: []
		}
	},
	transformState,
	styles,
	actions,
	actionHandlers,
	dispatches: {
		/**
		 * Dispatched when now button bare clicked
		 * @type {{}}
		 */
		NOW_BUTTON_BARE_CLICKED: {},
		/**
		 * Dispatched when now model opened set
		 * @type {{}}
		 */
		NOW_MODAL_OPENED_SET: {},
		/**
		 * Triggered when service catalog form value changed
		 * @type {{value: string, displayValue: string}}
		 */
		SC_FORM_VALUE_CHANGE: {},
		/**
		 * Dispatched when fetch deligation access
		 * @type {{user_sys_id: string, item_sys_id: string}}
		 */
		FETCH_DELEGATION_ACCESS: {},
		/**
		 * Dispatched when delegation data fetch success
		 * @type {{result: string}}
		 */
		DELEGATION_DATA_FETCH_SUCCESS: {},
		/**
		 * Dispatched when validate requested for user
		 * @type {{}}
		 */
		VALIDATE_REQUESTED_FOR: {},
		/**
		 * Dispatched when delegation data fetch failed
		 * @type {{}}
		 */
		DELEGATION_DATA_FETCH_FAILURE: {},
		/**
		 * Dispatched when hide requested for field message
		 * @type {{name: string}}
		 */
		HIDE_REQUEST_FOR_FIELD_MESSAGE: {},
		/**
		 * Dispatched when show requested for field message
		 * @type {{name: string, message: string, type: number}}
		 */
		SHOW_REQUEST_FOR_FIELD_MESSAGE: {}
	}
});
